<template>
  <div class="Admin-view">
    <!-- Content Header (Page header) -->
    <section>
      <div class="row">
        <div class="col-md-9">
          <h3 style="margin-top: 5px;">Driver List</h3>
        </div>
        <div class="col-md-3 text-right">
        <ol class="breadcrumb">
          <li><a href="#"><i class="fa fa-dashboard"></i> Home</a></li>
          <li class="active">Driver List</li>
        </ol>
      </div>
      </div>
    </section>
    <!-- /.content-header -->

    <!-- Content Body-->

    <div class="">
      <div class="col">
        <table id="user-table" class="display table-bordered" cellspacing="0" width="100%">
          <thead>
            <tr>
              <th style="width: 10%">Driver Type</th>
              <th style="width: 10%">Driver ID</th>
              <th style="width: 12%">Driver Name</th>
              <th style="width: 25%">Driver Address</th>
              <th style="width: 10%">Driver Mobile</th>
              <th style="width: 10%">VehicleNo</th>
              <th style="width: 7%">Mode</th>
              <th style="width: 7%">Rating</th>
              <th style="width: 7%">Status</th>
              <th style="width: 9%">Action</th>
            </tr>
          </thead>
          <tbody>
          </tbody>
        </table>
      </div>
    </div>

  </div>
</div>

</template>

<script>
  import axios from 'axios'
  export default {
    name: 'DriverListvw',
    data: {
      id: '',
      name: '',
      email: '',
      dataTable: null
    },
    methods: {
      check () {
        console.log('hii3')
      }
    },
    mounted: function mounted () {
      let users = []
      let route = this.$router
      let hostname = this.$hostName

      this.dataTable = $('#user-table').DataTable({})
      let uri1 = this.$hostName + '/api/driver/viewDriver'
      // const url = 'https://jsonplaceholder.typicode.com/users'
      axios.get(uri1)
      .then(data => {
        data.data.forEach(item => {
          users.push(item)
        })

        users.forEach(user => {
          this.dataTable.row.add([
            user.drivertype,
            user.driver_id,
            user.driver_name,
            user.driver_address,
            user.driver_phone,
            user.vehicle_number,
            user.status,
            user.rating,
            user.driverstatus,
            '<div style="padding:2px;"><button id="' + user.id + '" class="btn-success some-class">Edit</button></div><div style="padding:2px;"><button id="' + user.id + '" class="btn-danger some-class1">Delete</button></div>'
          ]).draw(false)
        })
      })

      $(document).on('click', '.some-class', function () {
        var sel = $(this).attr('id')
        route.push({ path: '/advanced/DriverList/edit/' + sel })
      })

      $(document).on('click', '.some-class1', function () {
        var sel = $(this).attr('id')
        var r = confirm('Are u want to Delete?')
        if (r === true) {
          axios.post(hostname + '/api/driver/deleteDriver', { id: sel }).then(res => {
            if (res.data.affectedRows > 0) {
              window.location.reload()
            } else alert("Driver Doesn't Exist")
          })
        }
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >

</style>



